.next-button {
    background-color: $color-gray;
    border: 1px solid $color-green-lighten;
    margin-top: 20px;
    color: #ffffff;
    font-weight: 800;
    border-radius: 25px;
    outline: none;
    transition: 1s;
    cursor: none;
    position: absolute;
    top: 25%;
    right: 3%;
    padding: 30px 10px;
    font-size: 30px;
    
    @include _xxl {
        position: relative;
        padding: 10px 0;
        width: 100%;
        font-size: 20px;
        top: 0;
        right: 0;
    }

    
    p {
        padding: 0;
        margin: 0;
        writing-mode: vertical-lr;
        text-orientation: upright;

        @include _xxl {
            writing-mode: inherit;
            text-orientation: inherit;
        }
    }

    

    @include _lg {
        margin-top: 40px;
    }

    &:focus {
        outline: none;
        border: 1px solid $color-green-lighten;
    }

    &.active {
        background-color: $color-violet;
        cursor: pointer;
    }
}